import React from 'react';
import moment from 'moment';
import './TickerPreview.scss';

export function TickerPreview({ message }) {
    return (
        <section className='ticker-preview'>
            <div className='user-profile'>
                <img src={`https://i.pravatar.cc/150?u=${message.id}`} alt='' />
                <h1>{message.name}</h1>
                
            </div>
            <div className='ticker-preview-details'>
            <h5> {moment(message.date).format('YYYY-MM-DD')}</h5>
                <p>{message.txt}</p>
            </div>
        </section>
    );
}
