import { Component } from 'react';
import { tickerService } from '../../services/ticker.service.js';
import { TickerList } from '../../cmps/TickerList/TickerList';
import './TickerApp.scss';

export class TickerApp extends Component {
    intervalId = null;
    state = {
        messages: null,
        time: '',
        index:null,
    };
    componentDidMount() {
        const messages = tickerService.getMessages();
        this.setState({ messages });
        this.intervalId = setInterval(() => {
            this.timer();
        }, 3000);
    }
    componentWillUnmount() {
        clearInterval(this.intervalId);
    }
    timer() {
        var { messages, index } = this.state;
        this.setState({ index: (index += 1) });
        var time = messages[index];
        this.setState({ time });
        messages.unshift(this.state.time);
        this.setState({ messages });
    }

    render() {
        const { messages } = this.state;
        if (!messages) return <div>Loading</div>;
        return (
            <section className='ticker-app'>
                <h1>welcome to My Ticker App</h1>
                <TickerList messages={messages} />
            </section>
        );
    }
}
