import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { TickerApp } from './pages/TickerApp/TickerApp';
import { HeroPage } from './pages/HeroPage/HeroPage';
import './App.css';

function App() {
    return (
        <Router>
            <div className='App'>
                <Switch>
                    <Route component={TickerApp} path='/ticker' />
                    <Route component={HeroPage} path='/' />
                </Switch>
            </div>
        </Router>
    );
}

export default App;
