import React from 'react';
import { TickerPreview } from '../TickerPreview';

import './TickerList.scss';

export function TickerList({ messages }) {
    return (
        <div className='ticker-list'>
            {messages &&
                messages
                    .map((message) => {
                        return (
                            <TickerPreview key={message.id} message={message} />
                        );
                    })
                    .slice(0, 3)}
        </div>
    );
}
